import {
	ClipboardDocumentIcon,
	PlusCircleIcon,
	TrashIcon,
} from "@heroicons/react/24/outline";
import {
	ClipboardDocumentIcon as ClipboardDocumentSolid,
	PlusCircleIcon as PlusCircleSolid,
	TrashIcon as TrashIconSolid,
} from "@heroicons/react/24/solid";
import { useRef } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import "./Ayah.css";
import Icon from "./Icon";
import surahToArr from "./utils/surahToArr";

export default function Ayah({ num = "", text, setSurah, surah }) {
	const englishToArabic = (s) =>
		s.toString().replace(/\d/g, (d) => "٠١٢٣٤٥٦٧٨٩"[d]);

	function setNewAyah(e) {
		console.log(e.target.outerText);
		setSurah((surah) => {
			let splitSurah = surahToArr(surah);
			splitSurah[num - 1] = e.target.innerText;
			return splitSurah.join("*");
		});
	}

	const spanRef = useRef(null);

	return (
		<>
			<span
				className="ayah-text"
				contenteditable="true"
				onBlur={setNewAyah}
				ref={spanRef}
			>
				{text}
				<div>
					<div
						onClick={() =>
							setSurah((surah) => {
								let splitSurah = surahToArr(surah);
								splitSurah.splice(num - 1, 1);
								return splitSurah.join("*");
							})
						}
					>
						<Icon
							Outlined={TrashIcon}
							Solid={TrashIconSolid}
							width="24px"
						/>
					</div>
					<div>
						<CopyToClipboard text={spanRef?.current?.textContent}>
							<span>
								<Icon
									Outlined={ClipboardDocumentIcon}
									Solid={ClipboardDocumentSolid}
									width="24px"
								/>
							</span>
						</CopyToClipboard>
					</div>
					<div
						onClick={() =>
							setSurah((surah) => {
								let splitSurah = surahToArr(surah);
								splitSurah.splice(num, 0, "*آيةٌ جَدْيدةٌ");
								return splitSurah.join("*");
							})
						}
					>
						<Icon
							Outlined={PlusCircleIcon}
							Solid={PlusCircleSolid}
							width="24px"
						/>
					</div>
				</div>
			</span>
			<span className="ayah-img">
				<span>{englishToArabic(num)}</span>
				<img src={require("./static/ayah.png")} alt="ayah" />
			</span>
		</>
	);
}
