import {
	CloudArrowUpIcon,
	PhotoIcon,
	PlusCircleIcon,
} from "@heroicons/react/24/outline";
import {
	CloudArrowUpIcon as CloudArrowUpSolid,
	PhotoIcon as PhotoIconSolid,
	PlusCircleIcon as PlusCircleSolid,
} from "@heroicons/react/24/solid";
import React, { useRef, useState } from "react";
import "./App.css";
import Ayah from "./Ayah";
import Icon from "./Icon";
import exportAsImage from "./utils/exportAsImage";
import surahToArr from "./utils/surahToArr";

function App() {
	const componentRef = useRef();
	const [title, setTitle] = useState("سُوْرَةُ الْفَلافِلْ");
	const [surah, setSurah] = useState("");
	const handleChange = (e) => {
		if (e.target.files && e.target.files.length > 0) {
			const selectedFile = e.target.files[0];
			const reader = new FileReader();
			reader.onload = (e) => {
				setSurah(e.target.result);
			};
			const blob = new Blob([selectedFile], { type: selectedFile.type });
			reader.readAsText(blob);
		}
	};

	return (
		<div className="app" ref={componentRef}>
			<div>
				<p contenteditable="true">{title}</p>
				<img src={require("./static/title.png")} alt="title" />
			</div>
			<div className="second-line">
				<div className="icons">
					<label>
						<input type="file" onChange={handleChange} />

						<Icon
							Outlined={CloudArrowUpIcon}
							Solid={CloudArrowUpSolid}
						/>
					</label>
					<button
						onClick={() =>
							exportAsImage(componentRef.current, "test")
						}
					>
						<Icon Outlined={PhotoIcon} Solid={PhotoIconSolid} />
					</button>
				</div>
				<img src={require("./static/bism.png")} alt="title" />
			</div>
			<p>
				{surahToArr(surah).map((ayah, index) => (
					<Ayah
						num={index + 1}
						text={ayah}
						setSurah={setSurah}
						surah={surah}
					/>
				))}
				<span
					className="svg-span"
					onClick={() =>
						setSurah((surah) => surah + "* آيةٌ جَدْيدةٌ")
					}
				>
					<Icon Outlined={PlusCircleIcon} Solid={PlusCircleSolid} />
				</span>
			</p>
		</div>
	);
}

export default App;
